.box {
  font-family: sans-serif;
  text-align: center;
  color: black;
  padding-bottom: 20px;
  border: solid 2px;
}
.b {
  background-color: red;
  padding: 5px;
  color: white;
}
.line {
  position: relative;
}
.line:before {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background: red;
  position: absolute;
  right: 40px;
  top: 50%;
  z-index: 2px;
}
.line:after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background: red;
  position: absolute;
  left: 40px;
  top: 50%;
}
.txt {
  transform: skew(-30deg);
  border: solid 1.6px;
  padding: 5px 50px;
}
::placeholder {
  text-align: center;
  transform: skew(30deg);
}
button {
  color: white;
  background-color: red;
  transform: skewX(-30deg);
  border: solid 1.6px;
  padding: 5px 50px;
}
.no {
  transform: skew(30deg);
}
